  box-shadow: 0 0 0 3px rgba(0, 226, 181, 0.15), 0 4px 12px rgba(0, 226, 181, 0.1);
}
.bg-hero {
  background: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(0, 226, 181, 0.08) 0%, transparent 50%),
    radial-gradient(ellipse 60% 40% at 80% 80%, rgba(6, 182, 212, 0.06) 0%, transparent 50%),
    linear-gradient(180deg, #fafbfc 0%, #f1f5f9 100%);
}
.bg-mesh {
  background: radial-gradient(at 40% 20%, rgba(0, 226, 181, 0.1) 0px, transparent 50%),
    radial-gradient(at 80% 0%, rgba(6, 182, 212, 0.08) 0px, transparent 50%),
    radial-gradient(at 0% 50%, rgba(139, 92, 246, 0.06) 0px, transparent 50%),
    radial-gradient(at 80% 50%, rgba(245, 158, 11, 0.05) 0px, transparent 50%),
    radial-gradient(at 0% 100%, rgba(0, 226, 181, 0.08) 0px, transparent 50%);
}
.hero-orb {
  position: absolute;
  border-radius: 50%;
  filter: blur(60px);
  animation: float 8s ease-in-out infinite;
}
.hero-orb-mint {
  background: rgba(0, 226, 181, 0.2);
}
.hero-orb-cyan {
  background: rgba(6, 182, 212, 0.15);
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: rgba(10, 15, 28, 0.15);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(10, 15, 28, 0.25);
}
@keyframes checkmark-draw {
  to {
    stroke-dashoffset: 0;
  }
}
.checkmark-animated {
  stroke-dasharray: 50;
  stroke-dashoffset: 50;
  animation: checkmark-draw 0.5s ease-out forwards;
}
.skeleton-mint {
  background: linear-gradient(90deg, rgba(0, 226, 181, 0.1) 0%, rgba(0, 226, 181, 0.2) 50%, rgba(0, 226, 181, 0.1) 100%);
  background-size: 200% 100%;
  animation: shimmer-sweep 1.5s infinite;
}
.text-gradient-mint {
  background: linear-gradient(135deg, #00e2b5 0%, #06b6d4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.card-enter {
  opacity: 0;
  transform: translateY(20px);
  animation: fade-in-up 0.5s cubic-bezier(0.16, 1, 0.3, 1) forwards;
}
@keyframes micro-bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.97);
  }
  100% {
    transform: scale(1);
  }
}
.tap-bounce:active {
  animation: micro-bounce 0.15s ease;
}
.bg-gradient-hero {
  background: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(0, 226, 181, 0.08) 0%, transparent 50%),
    radial-gradient(ellipse 60% 40% at 80% 80%, rgba(6, 182, 212, 0.06) 0%, transparent 50%),
    linear-gradient(180deg, #fafbfc 0%, #f1f5f9 100%);
}
.bg-gradient-subtle {
  background: linear-gradient(180deg, rgba(0, 226, 181, 0.02) 0%, transparent 50%),
    linear-gradient(180deg, #fafbfc 0%, #f8fafc 100%);
}
.bg-gradient-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.85) 100%);
}
.btn-glow {
  position: relative;
  overflow: hidden;
  transition-property: all;
  transition-timing-function: var(--tw-ease, var(--default-transition-timing-function));
  transition-duration: var(--tw-duration, var(--default-transition-duration));
  --tw-duration: 300ms;
  transition-duration: 300ms;
  background: linear-gradient(135deg, #00e2b5 0%, #00c9a0 100%);
}
.btn-glow:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(0, 226, 181, 0.35), 0 4px 12px rgba(0, 226, 181, 0.2);
}
.glow-soft {
  box-shadow: 0 8px 32px rgba(0, 226, 181, 0.1), 0 4px 16px rgba(10, 15, 28, 0.05);
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in-down {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in-left {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fade-in-right {
  from {
    opacity: 0;
    transform: translateX(10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes pulse-soft {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
@keyframes scale-in {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
  }
}
